body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #edebdd;
  color: #2c2c2c;
  overflow-x: hidden;
}

h1, h2, h3 {
  margin: 0.5rem 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
